                },
                {
                    label: 'Sample Service',
                    value: false
                },
                {
                    label: 'Sample Service',
                    value: false
                },
            ],
            parameters: [],
            outcome:{
                met:true,
                notMet:true,
                other: true
            }
        }
    },
    computed: {
        itemList() {
            return [
                'Item 1',
                'Item 2',
                'Item 3',
                'Item 4',
                'Item 5',
                'Item 6',
                'Item 7',
                'Item 8',
                'Item 9',
                'Item 10',
            ]
        },
    },
    methods: {
        addIssue(e) {
            this.programDesign.issues.push(e.target.value)
            e.target.value = null
        },
        removeIssue(i) {
            this.programDesign.issues.splice(i, 1)
        },
        addGoal(e) {
            this.programDesign.goals.push(e.target.value)
            e.target.value = null
        },
        removeGoal(i) {
            this.programDesign.goals.splice(i, 1)
        },
        selectPersonnel(e) {
            this.outcomeMeasurement.personnels.push(e.target.value)
            // e.target.value = 'selected'
        },
        removePersonnel(i) {
            this.outcomeMeasurement.personnels.splice(i, 1)
        },
        selectFunding(e) {
            this.outcomeMeasurement.fundings.push(e.target.value)
            // e.target.value = 'selected'
        },
        removeFunding(i) {
            this.outcomeMeasurement.fundings.splice(i, 1)
        },
        selectPartner(e) {
            this.outcomeMeasurement.partners.push(e.target.value)
            // e.target.value = 'selected'
        },
        removePartner(i) {
            this.outcomeMeasurement.partners.splice(i, 1)
        },
        addOther(e) {
            this.outcomeMeasurement.others.push(e.target.value)
            e.target.value = null
        },
        removeOther(i) {
            this.outcomeMeasurement.others.splice(i, 1)
        },
        addParameter(e) {
            let data = {
                title: e.target.value,
                inputType: 'number',
                value: null
            }
            this.parameters.push(data)
            e.target.value = null
        },
        removeParameter(i) {
            this.parameters.splice(i, 1)
        },
        submit(){
            console.log({'programDesign': this.programDesign})
            console.log({'outcomeMeasurement':this.outcomeMeasurement})
            console.log({'activities':this.activities})
            console.log({'parameters':this.parameters})
            console.log({'outcome':this.outcome})
        }
    }
}
